.vp-contributors {
  display: flex;
  flex-wrap: wrap;
  gap: 1rem 1.5rem;
  align-items: center;
  justify-content: flex-start;

  margin: 2rem 0 1rem;
}

.vp-contributor {
  display: flex;
  gap: 0.25rem;
  align-items: center;

  &::after {
    display: none !important;
  }

  &-avatar {
    object-fit: contain;
    width: 36px;
    height: 36px;
    border-radius: 50%;
  }
}

a.vp-contributor {
  text-decoration: none !important;
}
